* {
  font-size: 16px;
  line-height: 30px;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
  color: #1c1c1c;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  box-sizing: border-box;
  color: #1c1c1c;
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #1c1c1c;
  font-family: "Montserrat", sans-serif;
}

.box-shadow {
  box-shadow: 0 8px 13px 0 rgba(0, 0, 0, 0.16);
}

.site-bounds {
  max-width: 1366px;
  margin: 0 auto;
}

span.minilogo {
  display: inline-flex;
  transform: translateY(2px);
}

.sr-only {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

a.btn {
  color: white;
  text-decoration: none;
  transition: 0.3s ease all;
}
a.btn:hover {
  color: #fff;
}
a.btn:focus {
  color: #fff;
}

.btn.animated {
  font-size: 18px;
  letter-spacing: 2px;
  text-transform: uppercase;
  display: inline-block;
  text-align: center;
  font-weight: 600;
  padding: 10px 22px;
  border: 3px solid white;
  border-radius: 2px;
  position: relative;
  z-index: 2;
  background: transparent;
}
.btn.animated:before {
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: #78c9e5;
  z-index: -1;
}
.btn.animated:hover:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
}
.btn.animated:focus:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
}

.flex-grow {
  flex-grow: 1;
}

.box-bounds {
  max-width: 1400px;
  margin: auto;
  position: relative;
}

.video-popup-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: transparent;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  -webkit-backdrop-filter: blur(0);
  backdrop-filter: blur(0);
  transition: 250ms linear all;
}
.video-popup-container .video-popup {
  opacity: 0;
  transition: 150ms 250ms linear all;
}

.wpgdprc-consent-bar__notice .wpgdprc-consent-bar__notice,
.wpgdprc-consent-bar__notice p {
  color: white;
}

#cookie-law-info-bar {
  background-color: rgba(15, 46, 104, 0.8) !important;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

li.cli_settings_button {
  background: transparent !important;
}

@-webkit-keyframes headerIn {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes headerIn {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
header {
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 65px;
  right: 0;
  background-color: transparent;
  transform: translateY(-100%);
  -webkit-animation-name: headerIn;
  animation-name: headerIn;
  -webkit-animation-duration: 150ms;
  animation-duration: 150ms;
  -webkit-animation-delay: 150ms;
  animation-delay: 150ms;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}
header .header-inner {
  position: relative;
  z-index: 15;
}
header .header-inner:before {
  z-index: 10;
  transition: 150ms linear all;
  transform: translateY(-100%);
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(15, 46, 104, 0.8);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}
header.background .header-inner:before, header.trigger-bg .header-inner:before {
  transform: translateY(0);
  transition: 150ms linear all;
}
header .menu,
header .logo {
  z-index: 20;
}

.burger {
  display: none;
}

.main-menu a.nav-link {
  color: white;
  font-size: 14px;
  line-height: 18px;
  padding: 18px 16px;
}
.main-menu a.nav-link span[itemprop=name] {
  font-size: 14px;
  line-height: 18px;
  display: flex;
  align-items: flex-start;
  color: white;
}
.main-menu a.nav-link span[itemprop=name] span.minilogo {
  height: auto;
  width: 20px;
  display: inline-flex;
  margin: 0 5px;
}
.main-menu a.nav-link span[itemprop=name] span.minilogo svg {
  width: 20px;
  height: 15px;
}

ul.main-menu li a span[itemprop=name] {
  padding-bottom: 5px;
  padding-top: 5px;
  border-bottom: 1px solid transparent;
  transition: 150ms linear all;
}
ul.main-menu li.current-menu-item a span[itemprop=name] {
  border-color: white;
  transition: 150ms linear all;
}
ul.main-menu li a:hover span[itemprop=name] {
  border-color: white;
  transition: 150ms linear all;
}

@media only screen and (max-width: 420px) {
  header {
    height: 110px;
  }
  header .header-inner {
    flex-direction: column !important;
    align-items: center !important;
    justify-content: center !important;
  }
  header .header-inner .mobile-subnav {
    top: 90px;
    max-width: none;
  }
  header .header-inner .mobile-subnav ul li {
    text-align: center;
  }
}
h1,
.fake-h1,
h1 span,
.fake-h1 span {
  font-size: 54px;
  line-height: 66px;
  margin: 20px 0;
  color: white;
  font-weight: 900;
  font-family: "Montserrat";
}

h2,
h2 span {
  font-size: 18px;
  line-height: 24px;
  color: white;
  font-weight: 600;
}

h3,
h3 span {
  color: #0f2e68;
  font-weight: 900;
  font-family: "Montserrat";
  font-size: 24px;
  line-height: 1.2;
}

h4 {
  font-size: 18px;
  line-height: 24px;
  color: #78c9e5;
  font-weight: 600;
}

@media only screen and (max-width: 500px) {
  h1,
