.grid-container {
  margin-top: -150px;
}

.grid {
  gap: 20px;
}

.grid-item {
  transition: all 0.3s linear;
  min-width: 100%;
  min-height: 100%;
}
.grid-item span.no-results {
  opacity: 0;
  pointer-events: none;
  transition: 150ms linear all;
}
.grid-item.no-results span.no-results {
  opacity: 1;
  transition: 150ms linear all;
}

.is-animated {
  -webkit-animation: 0.6s zoom-in;
  animation: 0.6s zoom-in;
}

@-webkit-keyframes zoom-in {
  0% {
    transform: scale(0.1);
  }
  100% {
    transform: none;
  }
}

@keyframes zoom-in {
  0% {
    transform: scale(0.1);
  }
  100% {
    transform: none;
  }
}
