}
.home-box .home-box-link:hover .btn.animated:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
}
.home-box .home-box-link p {
  color: white;
  font-size: 14px;
  line-height: 20px;
}
.home-box:nth-child(1) .content-container {
