}
.home-box:nth-child(3) .content-container {
  background: #ffa300;
}
.home-box:nth-child(3) .btn.animated:before {
  background-color: #6b3077;
}
.home-box:nth-child(4) .content-container {
  background: #6b3077;
}
.home-box:nth-child(4) .btn.animated:before {
  background-color: #0f2e68;
}
.home-box .image-container {
  position: relative;
  overflow: hidden;
  padding-top: 58%;
}
.home-box .image-container img,
.home-box .image-container picture {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
}
.home-box .btn.animated {
  color: white;
  font-size: 14px;
  line-height: 14px;
}

