.card {
  border-radius: 0;
  border: 0;
}

.card-header {
  background: white;
  border-bottom: 0;
}
.card-header button a {
  color: #0f2e68;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
}
.card-header button[aria-expanded=true] a {
  text-decoration: underline;
}

.card-body {
  padding: 10px 0 0 0;
}
.card-body ul {
  list-style: none;
}
.card-body ul li a {
  color: #1c1c1c;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
}

.stick-to-top {
  position: -webkit-sticky;
  position: sticky;
  top: 72px;
}

.nav-arrow {
  width: 35px;
  height: 35px;
  border: 0;
  background: transparent;
  outline: none;
}
.nav-arrow:focus, .nav-arrow:active {
  outline: none;
}
.nav-arrow .arrow-down {
  width: 25px;
  height: 25px;
}
.nav-arrow .arrow-down svg {
  width: 25px;
  height: 25px;
  transform-origin: center center;
  transform: rotate(0);
  transition: 150ms linear all;
}
.nav-arrow.open .arrow-down svg {
  transform: rotate(180deg);
  transition: 150ms linear all;
}

.mobile-subnav {
  position: fixed;
  right: 0;
  width: 100%;
  max-width: 300px;
  top: 65px;
  background-color: rgba(15, 46, 104, 0.8);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  overflow: hidden;
  z-index: -1;
}
.mobile-subnav ul li {
  width: 100%;
}
.mobile-subnav ul li a,
.mobile-subnav ul li a span {
  color: white;
  line-height: 20px;
}
.mobile-subnav:open {
